<template>
    <div class="order_list">
        <div class="order_show">
            <el-card class="box-card-order" v-for="(item, index) in orderList" :key="index" :body-style="{'background-color': '#ffffff'}">
                <div slot="header" class="clearfix order_show_head">
                    <span style="margin:0 0 0 15px">订单号：{{item.orderInfo.OrderId}}</span>
                    <span style="margin:0 20px">下单时间：{{item.orderInfo.CreateTime | date}}</span>
                    <span>更新时间：{{item.orderInfo.UpdateTime | date}}</span>
                    <span v-if="isOffine" style="margin:0 0 0 15px">（线下订单）</span>
                    <el-popover
                            placement="bottom"
                            trigger="click"
                            style="float:right;height:19px">
                        <div v-if="(item.orderInfo.Status == 'UNPAID' || item.orderInfo.Status == 'WAIT_FOR_FINAL_PAY') && (item.orderInfo.Payment == 'UnionPay' || item.orderInfo.IsOffline == true || item.orderInfo.Payment == '')" class="order_show_selectitem">
                            <span v-if="!isOffine" style="display:inline-block;width:100%;height:100%;" @click="confirmM(item.orderInfo.Id, 'confirmGatherMoney')">确认收款</span>
                            <span v-else style="display:inline-block;width:100%;height:100%;" @click="confirmOffine(item.orderInfo.Id, item.orderInfo.Payment, item.orderInfo.PresellPayment)">确认收款</span>
                        </div>
                        <div v-if="item.orderInfo.Status == 'PRESELL_PAID' || item.orderInfo.Status == 'UNPAID' || item.orderInfo.Status == 'PRESELL_PAID' || item.orderInfo.Status == 'BEGIN_FINAL_PAY' || item.orderInfo.Status == 'WAIT_FOR_FINAL_PAY'"
                             @click="updateOrderPrice(item)"
                             class="order_show_selectitem">修改价格</div>
                        <div v-if="item.orderInfo.Status == 'UNPAID'" class="order_show_selectitem" @click="confirmM(item.orderInfo.Id, 'cancelOrder')">取消订单</div>
                        <div v-if="item.orderInfo.Status == 'UNPAID' || item.orderInfo.Status == 'EXPIRED'" class="order_show_selectitem" @click="confirmM(item.orderInfo.Id, 'overtime')">延长时间</div>
                        <div v-if="item.orderInfo.Status == 'UNDELIVERED'" class="order_show_selectitem">
                            <router-link :to="{path:'/ordermanage/order_sending',query:{OrderId:item.orderInfo.Id, isOffine:true}}" style="display: inline-block;width:100%;height: 100%;">
                                发货
                            </router-link>
                        </div>
                        <!-- <div v-if="item.orderInfo.Status == 'UNDELIVERED' && item.orderInfo.IsHostingOrder == false" class="order_show_selectitem"
                             @click="updateAddress(item.orderInfo.Id)">修改地址
                        </div> -->
                        <div v-if="item.orderInfo.Status == 'PAID'" class="order_show_selectitem" @click="updateOfflineOrderStatus(item.orderInfo.Id)">
                            待发货
                        </div>
                        <div v-if="item.orderInfo.Status == 'DELIVERED' && item.orderInfo.IsHostingOrder == false" class="order_show_selectitem"
                             @click="confirmM(item.orderInfo.Id, 'completed')">确认收货
                        </div>
                        <div class="order_show_selectitem">
                            <router-link :to="{path:'/offlinemanager/offline_order_detail',query:{OrderId:item.orderInfo.Id}}" style="display: inline-block;width:100%;height: 100%;">
                                查看详情
                            </router-link>
                        </div>
                        <div v-if="item.orderInfo.Status == 'DELIVERED' && item.orderInfo.IsHostingOrder == true" class="order_show_selectitem">
                            <router-link :to="{path:'/ordermanage/order_hosted',query:{OrderId:item.orderInfo.Id, isOffine:true}}" style="display: inline-block;width:100%;height: 100%;">
                                完成部署
                            </router-link>
                        </div>
                        <el-button slot="reference" class="order_show_button"><i class="el-icon-arrow-down"></i></el-button>
                    </el-popover>
                </div>
                <table class="order_show_table">
                    <tr>
                        <th>ID</th>
                        <th>用户名</th>
                        <th>商品ID</th>
                        <th>商品名</th>
                        <th>类型</th>
                        <th>单价</th>
                        <th>数量</th>
                        <th>优惠金额</th>
                        <th>总价</th>
                        <th>支付方式</th>
                        <th>状态</th>
                        <th>已支付总额</th>
                    </tr>
                    <tr>
                        <td>{{item.orderInfo.Id}}</td>
                        <td>{{item.orderInfo.Account}}</td>
                        <td>{{item.orderInfo.ProductId}}</td>
                        <td>{{item.orderInfo.ProductName}}</td>
                        <td>{{item.orderInfo.IsPresell?'预售':'全款'}}</td>
                        <td>{{item.orderInfo.ProductOriginPrice}}</td>
                        <td>{{item.orderInfo.Amount}}</td>
                        <td>{{item.orderInfo.Discount}}</td>
                        <td>{{item.orderInfo.Payment == 'UnionPay'?item.orderInfo.TotalShouldPayPrice:item.orderInfo.TotalShouldPayBtcPrice}}</td>
                        <td>
                            <div v-if="item.orderInfo.IsPresell">
                                <span v-if="item.orderInfo.PresellPayment == 'UnionPay'">人民币</span>
                                <span v-else-if="item.orderInfo.PresellPayment == 'BTC'">BTC</span>
                                <span v-else>{{item.orderInfo.PresellPayment}}</span>
                            </div>
                            <div v-else>
                                <span v-if="item.orderInfo.Payment == 'UnionPay'">人民币</span>
                                <span v-else-if="item.orderInfo.Payment == 'BTC'">BTC</span>
                                <span v-else>{{item.orderInfo.Payment}}</span>
                            </div>
                        </td>
                        <td>
                            <span>{{item.orderInfo | statusFilter}}</span>
                        </td>
                        <td>

                            <span v-if="item.orderInfo.IsPresell">
                                <span v-if="item.orderInfo.PresellPayment == 'UnionPay'">{{item.totalPrice}}</span>
                                <span v-else-if="item.orderInfo.PresellPayment == 'USD'">{{item.totalPrice}}</span>
                                <span v-else-if="item.orderInfo.PresellPayment == 'BTC'">{{item.totalPrice}}</span>
                                <span v-else>{{item.totalPrice}}</span>
                            </span>
                            <span v-else>
                                <span v-if="item.orderInfo.Payment == 'UnionPay'">{{item.totalPrice}}</span>
                                <span v-else-if="item.orderInfo.Payment == 'USD'">{{item.totalPrice}}</span>
                                <span v-else-if="item.orderInfo.Payment == 'BTC'">{{item.totalPrice}}</span>
                                <span v-else>{{item.totalPrice}}</span>
                            </span>
                        </td>
                    </tr>
                </table>
            </el-card>
            <!-- 分页 -->
            <el-pagination
                    background
                    :current-page="currentPage"
                    @current-change="getOrderChange"
                    :page-size="50"
                    layout="total, prev, pager, next, jumper"
                    :total="totalNo">
            </el-pagination>
        </div>
        <el-dialog
                title="修改价格"
                :visible.sync="dialogVisible"
                width="30%">
            <h4>ID</h4>
            <el-input
                    :placeholder="this.Id"
                    :disabled="true">
            </el-input>
            <h4>订单ID</h4>
            <el-input
                    :disabled="true"
                    :placeholder="this.OrderId">
            </el-input>
            <h4>人民币单价</h4>
            <el-input
                    placeholder="人民币单价"
                    v-model="input2"
                    clearable>
            </el-input>
            <h4>美元单价</h4>
            <el-input
                    placeholder="美元单价"
                    v-model="input3"
                    clearable>
            </el-input>
            <span slot="footer" class="dialog-footer">
                <el-button @click="dialogVisible = false">取 消</el-button>
                <el-button type="primary" @click="submitPrice">确 定</el-button>
            </span>
        </el-dialog>
        <!-- 确认收款弹窗 -->
        <el-dialog
                title="确认支付信息"
                :visible.sync="dialogVisibleOffline"
                width="34%"
                style="margin-top: -9vh;">
            <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
                <el-form-item v-if="this.paymentType == 'UnionPay'" label="收款方式" prop="type">
                    <el-radio-group v-model="ruleForm.type" >
                        <el-radio label='UnionPay'>人民币</el-radio>
                    </el-radio-group>
                </el-form-item>
                <el-form-item v-else-if="this.paymentType == 'USD'" label="收款方式" prop="type">
                    <el-radio-group v-model="ruleForm.type" >
                        <el-radio label='USD'>美元</el-radio>
                    </el-radio-group>
                </el-form-item>
                <el-form-item v-else-if="this.paymentType == 'BTC'" label="收款方式" prop="type">
                    <el-radio-group v-model="ruleForm.type" >
                        <el-radio label='BTC'>BTC</el-radio>
                    </el-radio-group>
                </el-form-item>
                <br>
                <!-- <div v-if="ruleForm.type == 'UnionPay'">
                    <el-form-item label="汇款人姓名" prop="bfyName" style="margin-bottom:0px">
                        <el-input v-model="ruleForm.bfyName" placeholder="请输入汇款人姓名" style="width:317px"></el-input>
                    </el-form-item>
                    <br>
                    <el-form-item label="汇款银行" prop="bfyBankName" style="margin-bottom:0px">
                        <el-input v-model="ruleForm.bfyBankName" placeholder="请输入汇款银行" style="width:317px"></el-input>
                    </el-form-item>
                    <br>
                    <el-form-item label="汇款卡号" prop="bankCode" style="margin-bottom:0px">
                        <el-input v-model="ruleForm.bankCode" placeholder="请输入汇款银行卡号" style="width:317px"></el-input>
                    </el-form-item>
                    <br>
                    <el-form-item label="汇款金额" prop="needPay" style="margin-bottom:0px">
                        <el-input v-model.number="ruleForm.needPay" placeholder="请输入汇款金额" style="width:317px"></el-input>
                    </el-form-item>
                </div>
                <div v-else-if="ruleForm.type == 'USD'">
                    <el-form-item label="remitter" prop="bfyName" style="margin-bottom:0px">
                        <el-input v-model="ruleForm.bfyName" placeholder="请输入汇款人姓名" style="width:417px"></el-input>
                    </el-form-item>
                    <br>
                    <el-form-item label="remitBank" prop="bfyBankName" style="margin-bottom:0px">
                        <el-input v-model="ruleForm.bfyBankName" placeholder="请输入汇款银行" style="width:417px"></el-input>
                    </el-form-item>
                    <br>
                    <el-form-item label="remittNum" prop="bankCode" style="margin-bottom:0px">
                        <el-input v-model="ruleForm.bankCode" placeholder="请输入汇款卡号" style="width:417px"></el-input>
                    </el-form-item>
                    <br>
                    <el-form-item label="remittAdd" prop="bankAddress" style="margin-bottom:0px">
                        <el-input v-model="ruleForm.bankAddress" placeholder="请输入汇款银行地址" style="width:417px"></el-input>
                    </el-form-item>
                    <br>
                    <el-form-item label="swiftCode" prop="swiftCode" style="margin-bottom:0px">
                        <el-input v-model="ruleForm.swiftCode" placeholder="请输入汇款银行代码" style="width:417px"></el-input>
                    </el-form-item>
                    <br>
                    <el-form-item label="remitAccount" prop="bfyAccountNumber" style="margin-bottom:0px">
                        <el-input v-model="ruleForm.bfyAccountNumber" placeholder="请输入汇款账号" style="width:417px"></el-input>
                    </el-form-item>
                    <br>
                    <el-form-item label="remitterAdd" prop="bfyAddress" style="margin-bottom:0px">
                        <el-input v-model="ruleForm.bfyAddress" placeholder="请输入汇款人地址" style="width:417px"></el-input>
                    </el-form-item>
                    <br>
                    <el-form-item label="payAmount" prop="needPay" style="margin-bottom:0px">
                        <el-input v-model.number="ruleForm.needPay" placeholder="请输入汇款金额" style="width:317px"></el-input>
                    </el-form-item>
                </div>
                <div v-else-if="ruleForm.type == 'BTC'">
                    <el-form-item label="比特币地址" prop="btcAddress" style="margin-bottom:0px">
                        <el-input v-model="ruleForm.btcAddress" placeholder="" style="width:317px"></el-input>
                    </el-form-item>
                    <br>
                    <el-form-item label="支付数量" prop="needPay" style="margin-bottom:0px">
                        <el-input v-model.number="ruleForm.needPay" placeholder="" style="width:317px"></el-input>
                    </el-form-item>
                </div> -->
                <!-- 订单状态 -->
                <el-form-item label="订单状态" prop="status" style="margin-top:20px">
                    <el-select v-model="ruleForm.status" placeholder="请选择订单状态" style="width:318px">
                        <el-option label="待支付尾款" value="WAIT_FOR_FINAL_PAY"></el-option>
                        <el-option label="已完成支付" value="PAID"></el-option>
                    </el-select>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button @click="dialogVisibleOffline = false">取 消</el-button>
                <el-button type="primary" @click="addPayInfo">确 定</el-button>
            </span>
        </el-dialog>
        <!-- 修改地址 -->
        <el-dialog
                title="修改地址"
                :visible.sync="dialogVisible1"
                width="30%">
            <h4>ID</h4>
            <el-input
                    :placeholder="this.adressInput1"
                    :disabled="true">
            </el-input>
            <h4>原收货人名字</h4>
            <el-input
                    :placeholder="this.adressInput2"
                    :disabled="true">
            </el-input>
            <h4>新收货人名字</h4>
            <el-input
                    placeholder="请输入"
                    v-model="adressInput3">
            </el-input>
            <h4>原收货手机号</h4>
            <el-input
                    :placeholder="this.adressInput4"
                    :disabled="true">
            </el-input>
            <h4>新收货手机号</h4>
            <el-input
                    placeholder="请输入"
                    v-model="adressInput5">
            </el-input>
            <h4>原收货电话</h4>
            <el-input
                    :placeholder="this.adressInput6"
                    :disabled="true">
            </el-input>
            <h4>新收货电话</h4>
            <el-input
                    placeholder="请输入"
                    v-model="adressInput7">
            </el-input>
            <h4>原地址</h4>
            <el-input
                    :placeholder="this.adressInput8"
                    :disabled="true">
            </el-input>
            <h4>新地址</h4>
            <el-input
                    placeholder="请输入"
                    v-model="adressInput9">
            </el-input>
            <h4>原邮编号</h4>
            <el-input
                    :placeholder="this.adressInput10"
                    :disabled="true">
            </el-input>
            <h4>新邮编号</h4>
            <el-input
                    placeholder="请输入"
                    v-model="adressInput11">
            </el-input>
            <span slot="footer" class="dialog-footer">
                <el-button @click="dialogVisible1 = false">取 消</el-button>
                <el-button type="primary" @click="submitAddress">确 定</el-button>
            </span>
        </el-dialog>
    </div>
</template>
<script>
    export default {
        name:'orderComponent',
        data(){
            return{
                paymentType:'',
                order_id:'',
                orderList: [],
                currentPage: 1,
                // 修改价格弹窗所用参数
                dialogVisible: false,
                dialogVisible1: false,
                dialogVisibleOffline:false,
                OrderId:0,
                input2: '',
                input3: '',
                Id: 0,
                adressInput1: '',
                adressInput2: '',
                adressInput3: '',
                adressInput4: '',
                adressInput5: '',
                adressInput6: '',
                adressInput7: '',
                adressInput8: '',
                adressInput9: '',
                adressInput10: '',
                adressInput11: '',
                ruleForm: {
                    type: 'UnionPay',
                    bfyName:'',
                    bfyBankName:'',
                    bankCode:'',
                    needPay:'',
                    bankAddress:'',
                    swiftCode:'',
                    bfyAccountNumber:'',
                    bfyAddress:'',
                    btcAddress:'',
                    status:''
                },
                rules: {
                    status: [
                        { required: true, message: '请选择支付类型', trigger: 'blur' }
                    ],
                    type: [
                        { required: true, message: '请选择支付类型', trigger: 'blur' }
                    ],
                    bfyName: [
                        { required: true, message: '请输入汇款人姓名', trigger: 'blur' }
                    ],
                    bfyBankName: [
                        { required: true, message: '请输入汇款银行', trigger: 'blur' }
                    ],
                    bankCode: [
                        { required: true, message: '请输入汇款银行卡号', trigger: 'blur' },
                        { min: 10, max: 30, message: '长度在 10 到 30 个字符', trigger: 'blur' }
                    ],
                    needPay: [
                        { required: true, message: '请输入数量', trigger: 'blur' },
                        { type: 'number', message: '单价必须为数字'}
                    ],
                    bankAddress: [
                        { required: true, message: '请输入银行地址', trigger: 'blur' }
                    ],
                    swiftCode: [
                        { required: true, message: '请输入银行代码', trigger: 'blur' }
                    ],
                    bfyAccountNumber: [
                        { required: true, message: '请输入汇款人账号', trigger: 'blur' }
                    ],
                    bfyAddress: [
                        { required: true, message: '请输入汇款人地址', trigger: 'blur' }
                    ],
                    btcAddress: [
                        { required: true, message: '请输入BTC汇款地址', trigger: 'blur' },
                        { min: 10, message: '长度不低于10个字符', trigger: 'blur' },
                        { pattern: /^[A-Za-z0-9]+$/, message: '不允许输入空格汉字' }
                    ]
                }
            }
        },
        // 获取从父组件传入的数据
        props: ['totalNo', 'isOffine'],
        methods: {
            updateOfflineOrderStatus(orderId){
                this.$api('/admin/offline/update_offline_order_status', {
                    orderId: orderId,
                    status: 'UNDELIVERED',
                    notice:'待发货处理成功'
                }).then(res => {
                    setTimeout('window.location.reload()',1500)
            });
            },
            confirmOffine(orderId,val2, val3){
                this.paymentType = ''
                if(val2 == 'UnionPay' || val3 == 'UnionPay'){
                    this.paymentType = 'UnionPay'
                    this.ruleForm.type = 'UnionPay'
                }else if(val2 == 'USD' || val3 == 'USD'){
                    this.paymentType = 'USD'
                    this.ruleForm.type = 'USD'
                }else if(val2 == 'BTC' || val3 == 'BTC'){
                    this.paymentType = 'BTC'
                    this.ruleForm.type = 'BTC'
                }
                this.dialogVisibleOffline = true
                this.order_id = orderId
            },
            addPayInfo(){
                if(this.ruleForm.status == ''){
                    return
                }
                this.dialogVisibleOffline = false
                this.$api('/admin/offline/update_offline_order_status', {
                    orderId: this.order_id,
                    status: this.ruleForm.status,
                    notice:'订单状态修改成功'
                }).then(res => {
                    setTimeout('window.location.reload()',1500)
            });
            },
            getOrderChange(val){
                let pageNo = val
                this.msg.$emit('paging',pageNo);
            },
            submitPrice(){
                if(this.input2 || this.input3){
                    this.$api('/admin/order/update_order_price', {
                        orderId: this.Id,
                        productOriginPrice: this.input2,
                        productOriginUsdPrice: this.input3,
                        notice:'修改价格成功'
                    }).then(res => {
                        // console.log(res)
                        this.dialogVisible = false
                    // 刷新页面，获取修改数据，有瞬间空白页待优化
                    setTimeout('window.location.reload()',1500)
                });
                }
            },
            submitAddress(){
                console.log('submitAddress')
                this.dialogVisible1 = false
                this.$api('/admin/order/update_order_address', {
                    orderId: this.adressInput1,
                    receiver: this.adressInput3?this.adressInput3:this.adressInput2,
                    addrPhone: this.adressInput7?this.adressInput7:this.adressInput6,
                    addrMobilephone: this.adressInput5?this.adressInput5:this.adressInput4,
                    address: this.adressInput9?this.adressInput9:this.adressInput8,
                    zipCode: this.adressInput11?this.adressInput11:this.adressInput10
                }).then(res => {
                    // console.log(res)
                    this.$message({
                    type: 'success',
                    message: '地址修改成功!'
                });
                this.dialogVisible = false
                // 刷新页面，获取修改数据，有瞬间空白页待优化
                // window.location.reload()
            });
            },
            updateOrderPrice(order){
                console.log(order)
                // .orderInfo.OrderId, item.orderInfo.Id, item.orderInfo.ProductOriginPrice, item.orderInfo.ProductOriginUsdPrice
                this.dialogVisible = true
                this.OrderId = order.orderInfo.OrderId
                this.Id = order.orderInfo.Id
                this.input2 = order.orderInfo.ProductOriginPrice
                this.input3 = order.orderInfo.ProductOriginUsdPrice
            },
            confirmM(orderId, type){
                // console.log(orderId, type)
                this.$confirm('是否确认操作?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    // 确定收款
                    this.updateOderInfo(orderId, type)
            }).catch(() => {
                    this.$message({
                    type: 'info',
                    message: '取消操作'
                });
            });
            },
            updateOderInfo(orderId, type){
                // console.log(orderId, type)
                // 确认收款,取消订单，延长时间
                this.$api('/admin/order/update_order_info', {
                    orderId:orderId,
                    operationType: type
                }).then(res => {
                    // console.log(res)
                    this.$message({
                    type: 'success',
                    message: '操作成功!'
                });
                // 刷新页面，获取修改数据，有瞬间空白页待优化
                window.location.reload()
            });
            },
            updateAddress(val){
                this.dialogVisible1 = true
                console.log(val)
                this.adressId = val
                this.adressInput1 = val
                this.$api('/admin/order/get_order_detail', {
                    orderId:val
                }).then(res => {
                    // console.log(res)
                let d = res.data.addressDetail
                // this.adressInput1 = d.ID
                this.adressInput2 = d.Receiver
                this.adressInput4 = d.MobilePhone
                this.adressInput6 = d.PhoneNumber
                this.adressInput8 = d.Address
                this.adressInput10 = d.ZipCode
            });
            }
        },
        mounted(){
            this.msg.$on('sendOrderList',(res)=>{
                this.orderList = res.orderList
            // console.log(res)
            // this.paymentType = this.orderList
        })
        }
    }
</script>
<style lang='scss' scoped>
    @import "../assets/css/titlecom.scss";
</style>


