<template>
    <div class="all_com">
        <div class="title_com">
            <span>线下订单管理</span>
        </div>
        <offlineUserInfo
                :accountId="this.accountId">

        </offlineUserInfo>
        <orderoffconpt
                :totalNo = "totalNo"
                :isOffine = "isOffine">
        </orderoffconpt>
    </div>
</template>
<script>
    import orderoffconpt from '../../components/orderoffconpt.vue'
    import offlineUserInfo from '../../components/offlineUserInfo.vue'
    export default {
        data () {
            return {
                pageNo: 1,
                orderList: [],
                totalNo: 0,
                isOffine: true, // 是否为线下订单参数
                filterMsg: {},
                searchMsg: {},
                account: ''
            }
        },
        components: {
            orderoffconpt,
            offlineUserInfo
        },
        watch: {
            pageNo: { // 深度监听分页条件变化，并刷新页面
                handler(val, oldVal){
                    this.getOfflineOder()
                },
                deep:true
            }
        },
        methods: {
            getOfflineOder(){
                this.$api('/admin/offline/get_offline_order_list', {
                    account: this.account,
                    pageNo:this.pageNo,
                    pageSize:50
                }).then(res => {
                    let d = res.data
                    // console.log(d)
                this.orderList = d.totalOrderList
                this.totalNo = d.page.totalNum
                let orderListMsg = {
                    value1:this.value1,
                    orderList: this.orderList
                }
                this.msg.$emit('sendOrderList',orderListMsg);
            });
            }
        },
        created(){
            this.account = this.$route.query.account
            this.accountId = this.$route.query.accountId
            // console.log(this.account)
            this.getOfflineOder()
        },
        mounted(){
            // 拿到分页条件
            this.msg.$on('paging',(res)=>{
                this.pageNo = res
        })
        }
    }
</script>
<style lang='scss' scoped>
    @import "../../assets/css/titlecom.scss";
</style>


